import React, { PureComponent, Suspense } from "react";
import { connect } from 'react-redux';
import { rID, activeTheme } from '../utils'
import { CloudHostUrl, TagboxUpload } from '../constants'
import { isTagShop, isTagbox, isSocialWall, isSocialEmbed, isEmbed } from "../actions/api";
// import { spotlightFromScoket } from '../actions/displayAction'

const Header = React.lazy(() => import('./Header'));
const ReviewHeader = React.lazy(() => import('./ReviewHeader'));
const Ticker = React.lazy(() => import('./Components/Tickers'));
const Filters = React.lazy(() => import('./Components/Filters'));
const ThemeRoute = React.lazy(() => import('./Themes'));
const OnsiteUploadButton = React.lazy(() => import('./Components/UserComponents/OnsiteUploadButton'));

class App extends PureComponent {
  constructor() {
    super();
    this.state = {
      containerSize: {
        width: null,
        height: null,
      },
      parentID: ''
    };
    this.containerRef = React.createRef();
  }

  componentDidMount() {
    activeTheme(this.props.ThemeId)
    window.addEventListener('resize', this.handleResize);
    if (this?.containerRef?.current) {
      this.setState({
        containerSize: {
          width: this?.containerRef?.current?.offsetWidth,
          height: this?.containerRef?.current?.offsetHeight,
        }
      })
    }
    // this.onLoadSpotLight()
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    const currentSize = this.containerRef.current
    if (currentSize) {
      this.setState({
        containerSize: {
          width: currentSize.offsetWidth,
          height: currentSize.offsetHeight,
        }
      })
    }
  }

  // onLoadSpotLight = () => {
  //   if (isSocialWall && !isEmbed) {
  //     const { postData } = this.props
  //     if (postData.completeDataObject && Object.keys(postData.completeDataObject).length) {
  //       var spotLightData = null
  //       Object.entries(postData.completeDataObject).forEach(([value]) => {
  //         if (value?.highlight == 1 && spotLightData == null) spotLightData = value
  //       });
  //       if (spotLightData && Object.keys(spotLightData).length) spotlightFromScoket(spotLightData)
  //     }
  //   }
  // }

  componentDidUpdate() {
    let p_class = document.querySelector('.tb_app_container')
    let P_id = rID ? rID : p_class?.parentNode?.id
    this.setState({ parentID: P_id })
  }
  
  render() {
    const { wall, reviewFilters, webFilters, postData, filter_tags } = this.props;
    const  { SnapUp, ThemeInfo } = wall
    const ThemeId = ThemeInfo.id

    const bannerStatus = wall?.Banner && Object.keys(wall.Banner).length  && wall?.Banner?.status === 1 ? true : false
    const customBannerStatus = wall?.BannerImage && Object.keys(wall.BannerImage).length  && wall?.BannerImage?.status === 1 ? true : false
    const headerPositionTop = (bannerStatus && wall?.Banner?.banner_position == 1) || customBannerStatus ? true : false;
    const headerPositionBottom = bannerStatus && wall?.Banner?.banner_position == 2 ? true : false;
    const QrStatus = (SnapUp.ruleStatus && SnapUp.QrCode.status) && (isTagShop && ![63, 64, 50].includes(ThemeInfo.id))

    const filters =  (![55, 52, 60, 16, 47, 57, 61].includes(ThemeId)) ? true : false;
    const isReviewBanner = wall.Banner && Object.keys(wall.Banner).length  && wall.Banner.status === 1 && wall.Banner.type == 6 && ![57, 84].includes(ThemeId)

    const web_Filter_Status = isTagbox ? wall.Personalization.filterStatus === 1 ? true : false : isTagbox && wall.Personalization.filterStatus === 1 && webFilters && webFilters.length > 1 ? true : false
    const teg_Filter_Status = filter_tags && filter_tags.length > 0 ? true : false
   // const spotlightStatus = isSocialWall && wall?.spotlight?.status ? true : false

    let brandNet = webFilters != '' && webFilters && webFilters.length ? webFilters.map(({ Network }) => Network?.id) : null;
    if (!isTagShop && wall?.Background?.image && wall.Background.image.includes(CloudHostUrl)) wall.Background['image'] = String(wall.Background.image).replace(TagboxUpload, "");


    const backgroundStyle = wall?.Background?.status === 1 && ThemeId !== 84 ? {
      backgroundImage: (wall?.Background?.url) ? `url(${wall?.Background?.url})` : '',
      backgroundColor: wall?.Background?.color
    } : null

    const reviewTheme = [63, 64].includes(ThemeId) ? true : false
    const tagshopHeader = wall?.Reviews?.headerStatus == 1 && reviewTheme ? true : false
    const reviewHeaderTop = ((isReviewBanner && headerPositionTop) || tagshopHeader)
    const ticker = isSocialWall && !isSocialEmbed && wall.CustomTicker?.status === 1
    const iswebFilters = (!isSocialWall && filters && (web_Filter_Status || teg_Filter_Status)) && ThemeId !== 84
    const isThemeRoute = (reviewTheme && wall.Reviews.themeStatus == 1) || !reviewTheme;

    return <div className="tb_app_container" style={{ minHeight: isEmbed ? [47, 16, 49, 55, 60, 52].includes(ThemeId) ? '100%' : `${this.props.postHeight}px` : null }} ref={this.containerRef}>
      { ticker && wall.CustomTicker.ticker_position == 1  ? <Suspense fallback={<></>}><Ticker CustomTicker={wall.CustomTicker} /></Suspense> : null}
      {headerPositionTop ? <Suspense fallback={<></>}><Header Banner={wall.Banner} BannerImage={wall.BannerImage} LogoImage={wall.LogoImage} /></Suspense> : ''}
      <div className="tb_app_wrapper" style={backgroundStyle}>
        { reviewHeaderTop ? <Suspense fallback={<></>}><ReviewHeader isHeader={tagshopHeader} reviewFilters={reviewFilters} appendData={postData.appendData} wall={wall} postData={postData} /></Suspense> : null}
        {QrStatus ? <Suspense fallback={<></>}> <OnsiteUploadButton SnapUp={wall.SnapUp} id={ThemeId} webId={ThemeInfo.webId} /> </Suspense> : null}
        { iswebFilters ? <Suspense fallback={<></>}> <Filters type={wall.Personalization.filter_type} webFilters={webFilters} filter_tags={filter_tags} appendData={postData.appendData} wall={wall}  postData={postData} /></Suspense>
            : '' }
        { isThemeRoute ? <Suspense fallback={<></>}><ThemeRoute {...this.props} brandingNetworks={brandNet} containerSize={this.state.containerSize} parentID={this.state.parentID} /></Suspense> : null}
        { isReviewBanner && headerPositionBottom ? <Suspense fallback={<></>}><ReviewHeader reviewFilters={reviewFilters} appendData={postData.appendData} wall={wall}  postData={postData} /></Suspense> : null} </div>
        { headerPositionBottom ? <Suspense fallback={<></>}><Header Banner={wall.Banner} BannerImage={wall.BannerImage} LogoImage={wall.LogoImage} /></Suspense> : ''}
        { ticker && wall.CustomTicker.ticker_position == 2 ? <Suspense fallback={<></>}><Ticker CustomTicker={wall.CustomTicker} /></Suspense> : null}
      </div>
  }
}

const mapStateToProps = state => {
  return { renderId: state.renderId }
}

export default connect(mapStateToProps, null)(App);
